@charset "UTF-8";
.UserDialog-Wrapper {
  /*全屏*/
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /*内容居中*/
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.4); }
  .UserDialog-Wrapper .UserDialog {
    border: 1px solid;
    border-radius: 4px;
    width: 400px;
    height: 250px;
    background: white;
    border: 1px solid #aaa;
    box-shadow: 0 0 1em rgba(0, 0, 0, 0.35);
    padding: 15px; }
    .UserDialog-Wrapper .UserDialog .signInOrSignUp > nav:first-child {
      padding: 1em;
      margin-left: -2em; }
    .UserDialog-Wrapper .UserDialog .signInOrSignUp > nav:first-child label {
      margin-left: 2em;
      cursor: pointer; }
    .UserDialog-Wrapper .UserDialog .forgotPassword h3 {
      padding: 1em; }
    .UserDialog-Wrapper .UserDialog .forgotPassword form {
      padding: 0 1em 1em; }
    .UserDialog-Wrapper .UserDialog form > .row {
      display: flex;
      padding: .3em 0; }
      .UserDialog-Wrapper .UserDialog form > .row label:first-child {
        width: 4em; }
      .UserDialog-Wrapper .UserDialog form > .row .actions {
        padding-left: 4em;
        display: flex;
        align-items: center; }
    .UserDialog-Wrapper .UserDialog a {
      display: flex;
      align-items: center;
      justify-content: space-between; }
